* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  html {
    font-size: 62.5%;
    scroll-behavior: smooth;
  }
  
  body {
    background-color: #000000;
    font-family: "Poppings", sans-serif;
    position: relative;
  }
  
  .container {
    max-width: 133rem !important;
    margin: 0 auto !important;
    padding: 0 2.5rem !important;
  }
  
  /* Firefox */
  * {
    scrollbar-width: thin;
    scrollbar-color: #fff #ffffff;
  }
  
  /* Chrome, Edge and Safari */
  *::-webkit-scrollbar {
    width: 10px;
    width: 10px;
  }
  
  *::-webkit-scrollbar-track {
    border-radius: 5px;
    background-color: white;
  }
  
  *::-webkit-scrollbar-track:hover {
    background-color: white;
  }
  
  *::-webkit-scrollbar-track:active {
    background-color: #a530ff;
  }
  
  *::-webkit-scrollbar-thumb {
    border-radius: 20px;
    background-color: black;
  }
  
  *::-webkit-scrollbar-thumb:hover {
    background-color: white;
  }
  
  *::-webkit-scrollbar-thumb:active {
    background-color: #a530ff;
  }
  
  .navbar {
    max-width: auto;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8.5px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background-color: #a530ff;
    z-index: 99999;
    margin: 0 auto;
  }
  .navbar__img {
    width: 14.5rem;
  }
  .navbar__img img {
    width: 100%;
    height: 100%;
  }
  .navbar__links {
    display: flex;
    list-style: none;
    gap: 8rem;
  }
  .navbar__links a {
    font-size: 1.6rem;
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    color: #fff;
    text-decoration: none;
    cursor: pointer;
    transition: all 0.3s;
  }
  .navbar__links a:hover {
    color: black;
  }
  @media (max-width: 1000px) {
    .navbar__links {
      display: none;
    }
  }
  .navbar__buttons {
    display: flex;
    gap: 2.5rem;
    font-size: 1.6rem;
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    align-items: center;
  }
  @media (max-width: 1000px) {
    .navbar__buttons {
      display: none;
    }
  }
  .navbar__buttons__sign-in {
    color: #010103;
    cursor: pointer;
    transition: all 0.3s;
  }
  .navbar__buttons__sign-in:hover {
    color: white;
  }
  .navbar__buttons__register {
    background-color: #fff;
    color: #000;
    cursor: pointer;
    padding: 1.5rem 3rem;
    border-radius: 3px;
    box-shadow: 0 10px 15px 0 rgba(79, 3, 114, 0.5);
    transition: all 0.3s;
  }
  .navbar__buttons__register:hover {
    color: white;
    box-shadow: 0 10px 15px 0 rgba(79, 3, 114, 0.5);
    background-color: #000;
  }
  .navbar__buttons a {
    text-decoration: none;
  }
  
  .mobile-hamb {
    font-size: 2.8rem;
    display: none;
    cursor: pointer;
    transition: all 0.3s;
  }
  .mobile-hamb:hover {
    color: #a530ff;
  }
  @media (max-width: 1000px) {
    .mobile-hamb {
      display: flex;
    }
  }
  
  .mobile-navbar {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -100%;
    background-color: #ffffff;
    z-index: 999999;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease-in-out;
  }
  .mobile-navbar__close {
    font-size: 3rem;
    position: absolute;
    top: 3.5rem;
    right: 3.5rem;
    cursor: pointer;
    transition: all 0.3s;
  }
  .mobile-navbar__close:hover {
    color:#a530ff;
  }
  .mobile-navbar__links {
    display: flex;
    flex-direction: column;
    list-style: none;
    font-size: 2.3rem;
    gap: 3rem;
    text-align: center;
  }
  .mobile-navbar__links li a {
    text-decoration: none;
    color: #010103;
    font-weight: 500;
    transition: all 0.3s;
  }
  .mobile-navbar__links li a:hover {
    color:#a530ff;
  }
  
  .open-nav {
    left: 0;
  }
  
  .hero-section {
    width: 100%;
    height: 97vh;
    background-color: black;
    position: relative;
  }
  
  .bg-shape {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }
  @media (max-width: 800px) {
    .bg-shape {
      display: none;
    }
  }
  
  .hero-content {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
  }
  @media (max-width: 800px) {
    .hero-content {
      justify-content: center;
    }
  }
  .hero-content__text {
    display: flex;
    flex-direction: column;
    z-index: 3;
    max-width: 50rem;
    margin-top: 5rem;
  }
  @media (max-width: 800px) {
    .hero-content__text {
      text-align: center;
      align-items: center;
    }
  }
  .hero-content__text h4 {
    font-size: 2.2rem;
    font-family: "Rubik", sans-serif;
    color: white;
  }
  .hero-content__text h1 {
    font-size: 5.2rem;
    font-family: "Poppins", sans-serif;
    font-weight: 700;
    color: white;
    line-height: 1.2;
    margin-top: 1rem;
    margin-bottom: 2.3rem;
  }
  .hero-content__text h1 span {
    color: #a530ff;
  }
  .hero-content__text p {
    font-size: 1.6rem;
    font-family: "Rubik", sans-serif;
    line-height: 1.6;
    color: #d194ff;
    margin-bottom: 4rem;
  }
  .hero-content__text__btns {
    display: flex;
    gap: 2rem;
    font-size: 1.6rem;
    font-family: "Rubik", sans-serif;
  }
  @media (max-width: 450px) {
    .hero-content__text__btns {
      flex-direction: column;
    }
  }
  .hero-content__text__btns a {
    text-decoration: none;
    color: white;
    font-weight: 700;
  }
  .hero-content__text__btns__book-ride {
    background-color: #a530ff;
    padding: 1.8rem 3rem;
    border-radius: 0.3rem;
    box-shadow: 0 10px 10px 0 #3f0769;
    transition: all 0.3s;
    border: 2px solid #a530ff;
  }
  @media (max-width: 450px) {
    .hero-content__text__btns__book-ride {
      padding: 1.8rem 8rem;
    }
  }
  .hero-content__text__btns__book-ride:hover {
    box-shadow: 0 10px 15px 0 #3f0769;
    background-color: #49057d;
  }
  .hero-content__text__btns__learn-more {
    color: black;
    background-color: black;
    padding: 1.8rem 3rem;
    border-radius: 0.3rem;
    border: 2px solid #a530ff;
    transition: all 0.3s;
  }
  .hero-content__text__btns__learn-more:hover {
    background-color: #49057d;
    color: #fff;
  }
  .hero-content__car-img {
    z-index: 2;
    position: absolute;
    right: -30px;
    width: 65%;
    margin-top: 5rem;
  }
  @media (max-width: 800px) {
    .hero-content__car-img {
      display: none;
    }
  }
  
  .scroll-up {
    position: fixed;
    font-size: 2.5rem;
    color: rgb(0, 0, 0);
    background-color: #a530ff;
    border: 3px solid white;
    width: 2rem;
    height: 2rem;
    bottom: 5rem;
    right: 5rem;
    z-index: 20;
    display: none;
    align-items: center;
    text-align: center;
    justify-content: center;
    padding: 2rem;
    cursor: pointer;
  }
  
  .show-scroll {
    display: flex;
  }
  
  .book-section {
    position: relative;
    background: black;
  }
  
  .book-content {
    margin: 0 auto;
  }
  .book-content__box {
    background-color: #000;
    width: 100%;
    padding: 10rem 4.5rem 5rem 5.5rem;
    box-shadow: 1px 1px 1px 5px #a530ff;
    height: 100%;
    position: relative;
    z-index: 4;
    color: white;
    border-radius: 1px;
    background-image: url("/src/images/book-car/book-bg.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    color: #fff;
  }
  @media (max-width: 1000px) {
    .book-content__box {
      padding: 4rem 2.5rem 5rem 2.5rem;
    }
  }
  .book-content__box h2 {
    font-size: 2.4rem;
    font-weight: 700;
    margin-bottom: 2.7rem;
  }
  .book-content__box .box-form {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto auto;
    gap: 2rem;
  }
  .book-content__box .box-form b {
    color: #a530ff;
  }
  @media (max-width: 1000px) {
    .book-content__box .box-form {
      grid-template-columns: 1fr 1fr;
    }
  }
  @media (max-width: 630px) {
    .book-content__box .box-form {
      grid-template-columns: 1fr;
    }
  }
  .book-content__box .box-form__car-type {
    display: flex;
    flex-direction: column;
  }
  .book-content__box .box-form__car-type label {
    font-size: 1.6rem;
    font-weight: 600;
    margin-bottom: 1.2rem;
  }
  .book-content__box .box-form__car-type label i {
    color: #a530ff;
  }
  .book-content__box .box-form__car-type select {
    font-size: 1.5rem;
    color: #000;
    font-family: "Rubik", sans-serif;
    border: 1px solid #ccd7e6;
    border-radius: 3px;
    font-weight: 400;
    padding: 1.2rem 1.3rem;
    outline: none;
  }
  .book-content__box .box-form__car-time {
    display: flex;
    flex-direction: column;
    position: relative;
  }
  .book-content__box .box-form__car-time input {
    outline: none;
    color: #000;
    padding-right: 3rem;
    border: 1px solid #ccd7e6;
  }
  .book-content__box .box-form__car-time label {
    font-size: 1.6rem;
    font-weight: 600;
    margin-bottom: 1.2rem;
  }
  .book-content__box .box-form__car-time label i {
    color: #000;
  }
  .book-content__box .box-form__car-time ::-webkit-datetime-edit {
    padding: 1.3rem 1.3rem;
  }
  .book-content__box .box-form__car-time ::-webkit-calendar-picker-indicator {
    cursor: pointer;
    font-size: 1.6rem;
  }
  .book-content__box button {
    padding: 1.3rem 1.3rem;
    height: -moz-fit-content;
    height: fit-content;
    align-self: self-end;
    border: none;
    font-family: "Rubik", sans-serif;
    font-size: 1.8rem;
    font-weight: 500;
    color: white;
    background-color: #a530ff;
    box-shadow: 0 10px 15px rgba(152, 49, 200, 0.5);
    cursor: pointer;
    transition: all 0.3s;
  }
  .book-content__box button:hover {
    box-shadow: 0 10px 15px rgba(79, 3, 114, 0.5);
  }
  
  .error-message {
    color: rgb(255, 0, 0);
    background-color: #fff;
    border-color: #74017e;
    padding: 1rem 1.4rem;
    margin-bottom: 1.8rem;
    margin-top: -1rem;
    border: 1px solid transparent;
    border-radius: 0.8rem;
    font-size: 1.6rem;
    font-weight: 500;
    display: none;
    justify-content: space-between;
    align-items: center;
  }
  .error-message i {
    cursor: pointer;
  }
  
  .booking-done {
    color: #2a6817;
    background-color: #c3fabe;
    border-color: #f5c6cb;
    padding: 1rem 1.4rem;
    margin-bottom: 1.8rem;
    margin-top: -1rem;
    border: 1px solid transparent;
    border-radius: 0.8rem;
    font-size: 1.6rem;
    font-weight: 500;
    display: none;
    justify-content: space-between;
    align-items: center;
  }
  .booking-done i {
    cursor: pointer;
  }
  
  .active-model {
    opacity: 1 !important;
    display: flex !important;
  }
  
  .model-overlay {
    opacity: 0;
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 999999999;
    top: 0;
    right: 0;
  }
  
  .booking-model {
    opacity: 0;
    display: none;
    flex-direction: column;
    position: fixed;
    overflow-x: hidden;
    overflow-y: scroll;
    z-index: 999999999999;
    top: 54%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 83rem;
    height: 100vh;
    background-color: rgb(255, 255, 255);
    padding-right: 2px;
    color: #ffffff;
  }
  @media (max-width: 800px) {
    .booking-model {
      width: 100%;
    }
  }
  @media (max-width: 600px) {
    .booking-model {
      top: 50%;
    }
  }
  .booking-model__title {
    display: flex;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    background-color: #a530ff;
    color: rgb(0, 0, 0);
    align-items: center;
  }
  .booking-model__title h2 {
    font-size: 2.4rem;
    text-transform: uppercase;
  }
  .booking-model__title i {
    font-size: 2.5rem;
    color: rgba(255, 255, 255, 0.919);
    cursor: pointer;
    transition: all 0.2;
  }
  .booking-model__title i:hover {
    color: white;
  }
  .booking-model__message {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    padding: 2rem 3rem;
    background-color: #000000;
  }
  .booking-model__message h4 {
    font-size: 1.9rem;
    font-weight: 700;
    color: hsl(0, 0%, 100%);
  }
  .booking-model__message h4 i {
    font-size: 2.5rem;
  }
  .booking-model__message p {
    font-size: 1.7rem;
    color: #ffffff;
    font-weight: 500;
    line-height: 1.6;
  }
  .booking-model__car-info {
    background-color: rgb(0, 0, 0);
    padding: 3rem 3rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    border-bottom: 1px solid rgb(0, 0, 0);
  }
  @media (max-width: 650px) {
    .booking-model__car-info {
      grid-template-columns: 1fr;
      text-align: center;
    }
  }
  .booking-model__car-info__dates {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  .booking-model__car-info__dates h5 {
    font-size: 1.8rem;
    color: #a530ff;
  }
  .booking-model__car-info__dates span {
    display: flex;
    gap: 1rem;
  }
  @media (max-width: 650px) {
    .booking-model__car-info__dates span {
      grid-template-columns: 1fr;
      text-align: center;
      justify-content: center;
    }
  }
  .booking-model__car-info__dates span i {
    font-size: 1.8rem;
    padding-top: 0.2rem;
    color: #000000;
  }
  @media (max-width: 650px) {
    .booking-model__car-info__dates span i {
      display: none;
    }
  }
  .booking-model__car-info__dates span h6 {
    font-size: 1.5rem;
    margin-bottom: 0.2rem;
  }
  .booking-model__car-info__dates span p {
    font-size: 1.6rem;
    color: #a530ff;
  }
  .booking-model__car-info__model {
    display: flex;
    flex-direction: column;
    gap: 3rem;
  }
  @media (max-width: 650px) {
    .booking-model__car-info__model {
      margin-top: 3.5rem;
    }
  }
  .booking-model__car-info__model h5 {
    font-size: 1.8rem;
    color: #a530ff;
  }
  .booking-model__car-info__model h5 span {
    color: #fff;
  }
  .booking-model__car-info__model img {
    width: 100%;
    height: auto;
  }
  .booking-model__person-info {
    padding: 3rem 3rem;
    background-color: rgb(0, 0, 0);
    display: flex;
    flex-direction: column;
  }
  .booking-model__person-info h4 {
    font-size: 1.8rem;
    text-transform: uppercase;
    color: #a530ff;
    margin-bottom: 2rem;
  }
  
  .input-time {
    text-align: center;
    width: 8rem;
    font-size: 14px;
    cursor: pointer;
  }
  
  .info-form {
    display: flex;
    flex-direction: column;
  }
  .info-form__checkbox {
    display: flex;
    gap: 1rem;
    align-items: center;
    font-size: 1.6rem;
    color: #565454;
    margin-top: 2rem;
    margin-bottom: 3rem;
  }
  .info-form__checkbox input {
    width: 1.6rem;
    height: 1.6rem;
  }
  .info-form__1col {
    grid-template-columns: 1fr !important;
  }
  .info-form__2col, .info-form__1col {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    padding: 1rem 0;
  }
  @media (max-width: 650px) {
    .info-form__2col, .info-form__1col {
      grid-template-columns: 1fr;
    }
  }
  .info-form__2col span, .info-form__1col span {
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
  }
  .info-form__2col span label, .info-form__1col span label {
    font-size: 1.6rem;
    font-weight: 500;
    color: #ffffff;
  }
  .info-form__2col span label b, .info-form__1col span label b {
    color: #a530ff;
  }
  .info-form__2col span input, .info-form__1col span input {
    padding: 14px 15px;
    background-color: #dbdbdb;
    color: #000000;
    font-size: 1.5rem;
    font-weight: 500;
    outline: none;
    border: none;
  }
  
  .reserve-button {
    background-color: #000000;
    margin: 0 -3rem;
    padding: 3rem;
    text-align: right;
  }
  @media (max-width: 650px) {
    .reserve-button {
      text-align: center;
    }
  }
  .reserve-button button {
    font-size: 2.4rem;
    color: white;
    font-weight: 700;
    background-color: #a530ff;
    border: 1px solid rgb(0, 0, 0);
    padding: 1.2rem 2rem;
    cursor: pointer;
    transition: all 0.2s;
  }
  .reserve-button button:hover {
    background-color: #06ec52;
  }
  
  .dates-div {
    display: flex;
    flex-direction: column;
    gap: 3.2rem;
  }
  
  .plan-section {
    background-color: #000;
    padding: 5.3rem 0;
  }
  
  .plan-container {
    display: flex;
    flex-direction: column;
  }
  .plan-container__title {
    margin: 0 auto;
    text-align: center;
    color: #a530ff;
  }
  .plan-container__title h3 {
    font-size: 2.4rem;
    font-family: "Rubik", sans-serif;
    font-weight: 500;
  }
  .plan-container__title h2 {
    font-size: 4.2rem;
    font-family: "Poppins", sans-serif;
    margin: 1.3rem 0 3rem 0;
  }
  .plan-container__boxes {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto;
    margin-top: 3.7rem;
    padding: 0 3rem;
  }
  @media (max-width: 1021px) {
    .plan-container__boxes {
      grid-template-columns: 1fr 1fr;
      row-gap: 2rem;
    }
  }
  @media (max-width: 800px) {
    .plan-container__boxes {
      grid-template-columns: 1fr;
      margin-top: 1rem;
    }
  }
  .plan-container__boxes__box {
    text-align: center;
    padding: 1rem 6rem;
  }
  @media (max-width: 500px) {
    .plan-container__boxes__box {
      padding: 1rem 1rem;
    }
  }
  .plan-container__boxes__box img {
    width: 17rem;
    height: auto;
  }
  .plan-container__boxes__box h3 {
    font-size: 2.4rem;
    margin-bottom: 1rem;
  }
  .plan-container__boxes__box p {
    font-size: 1.6rem;
    font-family: "Rubik", sans-serif;
    color: #fff;
    line-height: 1.43;
  }
  
  .pick-section {
    background-color: #000;
    padding: 10rem 0;
  }
  
  .pick-container {
    display: flex;
    flex-direction: column;
  }
  .pick-container__title {
    margin: 0 auto;
    text-align: center;
    color: #a530ff;
    max-width: 50rem;
    margin-bottom: 5rem;
  }
  .pick-container__title p {
    font-size: 1.6rem;
    font-family: "Rubik", sans-serif;
    color: #fff;
    line-height: 1.5;
  }
  .pick-container__title h3 {
    font-size: 2.4rem;
    font-family: "Rubik", sans-serif;
    font-weight: 500;
  }
  .pick-container__title h2 {
    font-size: 4.2rem;
    font-family: "Poppins", sans-serif;
    margin: 0.5rem 0 1rem 0;
  }
  .pick-container__car-content {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
  }
  @media (max-width: 1050px) {
    .pick-container__car-content {
      flex-direction: column;
      gap: 5rem;
    }
  }
  
  .pick-box {
    display: flex;
    flex-direction: column;
    gap: 0.7rem;
  }
  .pick-box button {
    font-size: 2rem;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    border: none;
    cursor: pointer;
    padding: 1.5rem 2.5rem;
    background-color: #fff;
    transition: all 0.2s;
    text-align: left;
